<template>
  <div v-loading="loading" element-loading-text="">
    <div class="handle-box">
      <div class="btn">
        <el-button type="primary" @click="dialogStudent = true">EXCEL</el-button>
      </div>
    </div>

    <!-- Excel -->
    <el-dialog title="" :visible.sync="dialogStudent" width="500px" :close-on-click-modal="false">
      <div class="handle-box">
        <div class="btn">
          <el-select v-model="grade" placeholder="" @change="gradeOnChange">
            <el-option v-for="item in gradeList" :key="item.id" :label="item.title" :value="item.id"></el-option>
          </el-select>
        </div>
        <div class="btn">
          <el-select v-model="class_id" placeholder="">
            <el-option v-for="item in classList" :key="item.id" :label="item.title" :value="item.id"></el-option>
          </el-select>
        </div>
      </div>
      <div class="btn">
        <el-button type="primary" icon="el-icon-download"></el-button>
      </div>
      <el-upload class="upload-demo" ref="upload" drag action="https://school.fengniaotuangou.cn/import/students"
        :data="classData" :on-remove="handleRemove" :file-list="fileList" :auto-upload="false"
        :on-success="handleSuccess" :headers="uploadHeader">
        <i class="el-icon-upload"></i>
        <div class="el-upload__text">
          
          <em></em>
        </div>
        <div slot="tip" class="el-upload__tip">EXCEL</div>
      </el-upload>
      <div class="upload-btn">
        <el-button type="primary" @click="upload"></el-button>
      </div>
    </el-dialog>

    <el-table :data="tableDate" ref="multipleTable" class="aip-table" border :header-cell-style="{background:'#f0f0f0'}"
      max-height="620">
      <el-table-column prop="id" label="ID"></el-table-column>
      <el-table-column prop="file_name" label=""></el-table-column>
      <el-table-column prop="created_at" label=""></el-table-column>
      <el-table-column prop="status" label="">
        <template slot-scope="scope">
          <span v-if="scope.row.status == 1"></span>
          <span v-else-if="scope.row.status == 2"></span>
        </template>
      </el-table-column>
    </el-table>

    <div class="block">
      <el-pagination @current-change="currentChange" :current-page.sync="current" :page-sizes="[10, 20, 30, 40, 50]"
        :page-size="size" layout="sizes, prev, pager, next, jumper" :total="total" @size-change="sizeChange">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import API from "@/api/index.js";

  export default {
    name: "studentadd",
    data() {
      return {
        loading: true,
        dialogStudent: false,
        grade: "",
        gradeList: [],
        class_id: "",
        classList: [],
        fileList: [],
        classData: {
          class_id: "", // 
        },

        tableDate: [],
        current: 1,
        size: 10,
        total: 0,
      };
    },
    computed: {
      uploadHeader() {
        return {
          token: localStorage.getItem("token"),
        };
      },
    },
    mounted() {
      this.getGrade();
      this.getList(this.current, this.size);
    },
    methods: {
      // 
      getList(cur, list) {
        var self = this;
        API.imports(cur, list, 2)
          .then((res) => {
            console.log(res);
            self.loading = false;
            self.tableDate = res.data;
            self.total = res.total;
          })
          .catch((err) => {
            self.loading = false;
          });
      },
      // 
      currentChange(val) {
        var self = this;
        self.current = val;
        self.loading = true;
        self.getList(val, self.size);
      },
      sizeChange(val) {
        var self = this;
        self.size = val;
        self.loading = true;
        self.getList(1, val);
        self.current = 1;
      },
      // 
      getGrade() {
        var self = this;
        API.grades().then((res) => {
          self.gradeList = res.data;
        });
      },
      getClass(grade_id) {
        var self = this;
        API.classes(1, 1000, grade_id).then((res) => {
          self.classList = res.data;
        });
      },
      // 
      gradeOnChange(value) {
        var self = this;
        self.grade = value;
        self.getClass(value);
      },
      // 
      // 
      upload() {
        this.classData.class_id = this.class_id;
        this.$refs.upload.submit();
      },
      handleRemove(file, fileList) {
        console.log(file, fileList);
      },
      handleSuccess(file, fileList) {
        this.dialogStudent = false;
        this.$message.success("");
        this.getList(this.current, this.size);
        this.fileList = [];
      },
    },
  };
</script>


<style scoped>
  .upload-btn {
    margin-top: 10px;
  }
</style>