<template>
  <div v-loading="loading" element-loading-text="">
    <div class="handle-box">
      <div class="btn">
        <el-button type="primary" @click="addStudent"></el-button>
      </div>
      <div class="btn">
        <el-input placeholder="/" v-model="student" @keyup.enter.native="search(student)"
          class="input-with-select">
