            <el-option v-for="item in typeList" :key="item.value" :label="item.label" :value="item.value"></el-option>
          </el-select>
          <el-button slot="append" icon="el-icon-search" @click="search(student)"></el-button>
        </el-input>
      </div>
      <div class="btn" v-if="role == 'admin'">
        :
        <el-select v-model="school_name" placeholder="" @change="selcScl">
          <el-option v-for="item in schoolList" :key="item.value" :label="item.name" :value="item.id">
          </el-option>
        </el-select>
      </div>
    </div>

    <el-dialog title="" :visible.sync="dialogStudent" width="700px" :close-on-click-modal="false" @close=close>
      <div class="box">
        <el-form :model="form" label-width="100px">
          <el-form-item label="">
            <el-select v-model="grade" placeholder="" @change="gradeOnChange">
              <el-option v-for="item in gradeList" :key="item.id" :label="item.title" :value="item.id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="classValue" placeholder="" @change="classOnChange">
              <el-option v-for="item in classList" :key="item.id" :label="item.title" :value="item.id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="form.name" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="form.number" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-radio-group v-model="form.sex">
              <el-radio :label="1"></el-radio>
              <el-radio :label="2"></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="form.age" placeholder=""></el-input>
          </el-form-item>
          <div class="submit">
            <el-form-item>
              <el-button type="primary" @click="newStudent"></el-button>
            </el-form-item>
          </div>
        </el-form>
      </div>
    </el-dialog>

    <el-table :data="tableData" ref="multipleTable" border :header-cell-style="{ background: '#f0f0f0' }"
      max-height="620">
      <el-table-column prop="id" label="ID"></el-table-column>
      <el-table-column prop="number" label=""></el-table-column>
      <el-table-column prop="name" label=""></el-table-column>
      <el-table-column prop="school.name" label=""></el-table-column>
      <el-table-column prop="face_image" label="">
        <template slot-scope="scope">
          <el-popover placement="top-start" title trigger="click">
            <img :src="scope.row.face_image" style="max-width: 800px; max-height: 800px" />
            <img slot="reference" :src="scope.row.face_image" style="max-width: 180px; max-height: 80px" />
          </el-popover>
        </template>
      </el-table-column>
      <el-table-column prop="sex" label="" width="100px">
        <template slot-scope="scope">
          <span v-text="scope.row.sex == '1' ? '' : ''"></span>
        </template>
      </el-table-column>
      <el-table-column prop="age" label="()" width="100px"></el-table-column>
      <el-table-column prop="updated_at" label=""></el-table-column>
      <el-table-column label="" fixed="right">
        <template slot-scope="scope">
          <el-dropdown>
            <el-button type="primary">
              
              <i class="el-icon-arrow-down el-icon--right"></i>
            </el-button>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item>
                <el-button size="mini" type="primary" @click="handleFace(scope.$index, scope.row)"></el-button>
              </el-dropdown-item>
              <el-dropdown-item>
                <el-button size="mini" type="primary" @click="handleFamily(scope.$index, scope.row)"></el-button>
              </el-dropdown-item>
              <el-dropdown-item>
                <el-button size="mini" type="primary" @click="handlePushFace(scope.$index, scope.row)"></el-button>
              </el-dropdown-item>
              <el-dropdown-item>
                <el-button size="mini" type="danger" @click="handleForbidden(scope.$index, scope.row)"></el-button>
              </el-dropdown-item>
              <el-dropdown-item>
                <el-button size="mini" type="primary" @click="handleCreateFace(scope.$index, scope.row)">
                </el-button>
              </el-dropdown-item>
              <el-dropdown-item>
                <el-button size="mini" type="primary" @click="handleServe(scope.$index, scope.row)"></el-button>
              </el-dropdown-item>
              <el-dropdown-item>
                <el-button size="mini" type="primary" @click="handleFaceLogs(scope.$index, scope.row)">
                </el-button>
              </el-dropdown-item>
              <el-dropdown-item>
                <el-button size="mini" type="primary" @click="handleEdit(scope.$index, scope.row)"></el-button>
              </el-dropdown-item>
              <el-dropdown-item>
                <el-button type="danger" size="mini" @click="handleDel(scope.$index, scope.row)"></el-button>
              </el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <div class="block">
      <el-pagination @current-change="currentChange" :current-page.sync="current" :page-sizes="[10, 20, 30, 40, 50]"
        :page-size="size" layout="sizes, prev, pager, next, jumper" :total="total" @size-change="sizeChange">
      </el-pagination>
    </div>

    <!--  -->
    <el-dialog title="" :visible.sync="dialogFamily" :close-on-click-modal="false" width="900px">
      <div class="input_box">
        <el-input placeholder="" v-model="keyword" @keyup.enter.native="searchFamily(keyword)"
          class="input-with-select" width="100">
          <el-button slot="append" icon="el-icon-search" @click="searchFamily(keyword)"></el-button>
        </el-input>
      </div>
      <el-table :data="familyList" style="width: 100%">
        <el-table-column prop="user_id" label="ID" width="180">
        </el-table-column>
        <el-table-column prop="name" label="" width="180">
        </el-table-column>
        <el-table-column prop="address" label=""> </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button type="primary" size="mini" @click="bindFamily(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-dialog>

    <el-dialog title="" :visible.sync="dialogFace" :close-on-click-modal="false" width="500px">
      <div class="box">
        <el-form :model="familyForm">
          <!--  -->
          <el-form-item label="">
            <el-radio-group v-model="familyForm.only_in">
              <el-radio :label="1"></el-radio>
              <el-radio :label="2"></el-radio>
            </el-radio-group>
          </el-form-item>
          <div class="tips">
            <p><span></span></p>
          </div>
          <el-form-item label="">
            <el-upload action="https://upload-z2.qiniup.com" ref="upload" :limit="1" :before-upload="beforeAvatarUpload"
              :auto-upload="false" :on-success="handleAvatarSuccess" :on-remove="handleRemove" :on-exceed="handleExceed"
              :on-change="handleChange" :data="imgData">
              <el-button size="small" type="primary"></el-button>
            </el-upload>
            <div v-if="hasNewImage" style="color: red; font-size: 12px">
              * 
            </div>

            <div class="up-img" v-if="old_href">
              <span style="display: flex; justify-items: center; color: #409eff"></span>
              <img class="pic-box" :src="old_href" />
            </div>
            <div class="up-img" v-if="familyForm.href">
              <span style="display: flex; justify-items: center; color: #67c23a"></span>
              <img class="pic-box" :src="familyForm.href" />
            </div>
            <div class="up-img" v-else>
              <img class="pic-box" :src="change_href" />
            </div>
          </el-form-item>
          <div class="submit">
            <el-form-item>
              <el-button type="primary" @click="changeFace"></el-button>
            </el-form-item>
          </div>
        </el-form>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog :visible.sync="dialogPushFace" title="" width="20%" align="center" :close-on-click-modal="false">
      <div style="font-size: 20px; margin-bottom: 30px"></div>
      <el-input placeholder="uuid" v-model="uuid" class="input-with-select input" @change="inputUuid" v-if="hasUuid">
      </el-input>
      <span>
        <el-button type="primary" @click="pushFace"></el-button>
        <el-button type="danger" @click="dialogPushFace = false"></el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="dialogFaceLogs" :close-on-click-modal="false" width="1000px">
      <div class="box">
        <el-table :data="FaceLogsDate" border :header-cell-style="{ background: '#f0f0f0' }" max-height="620">
          <el-table-column prop="time" label=""></el-table-column>
          <el-table-column prop="temp" label=""></el-table-column>
          <el-table-column prop="direction" label="">
            <template slot-scope="scope">
              <span v-text="scope.row.direction == '1' ? '' : ''"></span>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div class="block">
        <el-pagination @current-change="logsCurrentChange" :current-page.sync="logsCurrent"
          :page-sizes="[10, 20, 30, 40, 50]" :page-size="logsSize" layout="sizes, prev, pager, next, jumper"
          :total="logsTotal" @size-change="logSizeChange"></el-pagination>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog :visible.sync="dialogDel" title="" width="20%" align="center" :close-on-click-modal="false">
      <div style="font-size: 20px; margin-bottom: 30px"></div>
      <span>
        <el-button type="primary" @click="toDel"></el-button>
        <el-button type="danger" @click="dialogDel = false"></el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog :visible.sync="dialogForbidden" title="" width="20%" align="center" :close-on-click-modal="false">
      <div style="font-size: 20px; margin-bottom: 30px"></div>
      <span>
        <el-button type="primary" @click="forbiddenFace"></el-button>
        <el-button type="danger" @click="dialogForbidden = false"></el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog :visible.sync="dialogCreateFace" title="" width="20%" align="center" :close-on-click-modal="false">
      <div style="font-size: 20px; margin-bottom: 30px"></div>
      <span>
        <el-button type="primary" @click="createFace"></el-button>
        <el-button type="danger" @click="dialogCreateFace = false"></el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="dialogOpenedServe" width="80%">
      <div class="box">
        <el-table :data="serviceList" border :header-cell-style="{ background: '#f0f0f0' }">
          <el-table-column prop="product_id" label="ID"></el-table-column>
          <el-table-column prop="user_id" label="ID"></el-table-column>
          <el-table-column prop="user_name" label=""></el-table-column>
          <el-table-column prop="title" label=""></el-table-column>
          <el-table-column prop="time" label="()"></el-table-column>
          <el-table-column prop="created_at" label=""></el-table-column>
        </el-table>
      </div>
    </el-dialog>

  </div>
</template>

<script>
  import API from "@/api/index.js";
  import md5 from "blueimp-md5";
  import axios from "axios";
  import DATE from "@/utils/date.js";

  let href;
  export default {
    name: "studentlist",
    data() {
      return {
        loading: true,
        // 
        dialogStudent: false, // 
        grade: "",
        gradeList: [],
        classValue: "",
        classList: [],
        form: {
          name: "",
          number: "",
          sex: 1,
          age: "",
          class_id: "",
          grade_id: "",
        },
        dialogFace: false, // 
        dialogDel: false, // 
        id: "", // id
        dialogFamily: false, // 
        familyList: [],
        familyData: [],
        list: [],
        // nickname: '',
        disabled: false,
        imgData: {
          key: "",
          token: "",
        },
        fileName: "",
        suffix: "",
        qiniuaddr: "https://tu.fengniaotuangou.cn", // 
        familyForm: {
          href: "",
          only_in: "",
          id: "",
        },
        old_href: "", // 
        change_href: "",
        hasNewImage: false,
        new_file: "",
        student: "", // --
        type: 2, // 
        typeList: [{
            // 
            value: 1,
            label: "",
          },
          {
            value: 2,
            label: "",
          },
        ],
        typeDisabled: false,
        bindFormFamily: {
          user_id: "",
          student_id: "",
        },
        FaceLogsDate: [],
        dialogFaceLogs: false,
        logsCurrent: 1,
        logsSize: 10,
        logsTotal: 0,
        log_id: "",

        tableData: [],
        face_image: "",
        // 
        current: 1,
        total: 0,
        size: 10,
        dialogPushFace: false,
        dialogForbidden: false,
        forbidden_id: "",
        openFace_id: "",

        dialogCreateFace: false,
        dialogOpenedServe: false,
        serviceList: [],
        keyword: "",
        uuid: "",
        hasUuid: false,

        permissions: localStorage.getItem("permissions"),
        role: localStorage.getItem("username"),
        schoolList: [],
        school_name: '',
        school_id: ''
      };
    },
    mounted() {
      this.getStudent(this.current, this.size);
      this.getGrade();
      // this.getFamily();
      this.getQiniuToken();
      this.getScl();
    },
    methods: {
      // 
      getStudent(cur, list) {
        var self = this;
        API.students(cur, list)
          .then((res) => {
            self.loading = false;
            self.tableData = res.data;
            self.total = res.total;
          })
          .catch((err) => {
            self.loading = false;
          });
      },
      // 
      currentChange(val) {
        var self = this;
        self.current = val;
        self.loading = true;
        if (self.student) {
          switch (self.type) {
            case 1:
              var number = self.student;
              self.fuvSearch(val, self.size, name, number);
              break;
            case 2:
              var name = self.student;
              self.fuvSearch(val, self.size, name, number);
          }
        } else if (self.school_id) {
          self.getStu(val, self.size);
        } else {
          self.getStudent(val, self.size);
        }
      },
      sizeChange(val) {
        var self = this;
        self.size = val;
        self.loading = true;
        if (self.student) {
          switch (self.type) {
            case 1:
              var number = self.student;
              self.fuvSearch(1, val, name, number);
              break;
            case 2:
              var name = self.student;
              self.fuvSearch(1, val, name, number);
          }
        } else if (self.school_id) {
          self.getStu(1, val);
        } else {
          self.getStudent(1, val);
        }
        self.current = 1;
      },

      // 
      changeType(val) {
        var self = this;
        self.typeDisabled = true;
        self.student = "";
      },
      fuvSearch(cur, list, name, number) {
        var self = this;
        API.searchName(cur, list, name, number).then((res) => {
          self.loading = false;
          self.$message.success("");
          self.tableData = res.data;
          self.total = res.total;
        });
      },
      search() {
        var self = this;
        self.loading = true;
        self.school_name = '';
        self.current = 1;
        if (self.type == 1) {
          var number = self.student;
          self.fuvSearch(self.current, self.size, name, number);
        }
        if (self.type == 2) {
          var name = self.student;
          self.fuvSearch(self.current, self.size, name, number);
        }
      },
      // 
      getScl() {
        var self = this;
        API.schools(1, 1000).then((res) => {
          self.schoolList = res.data;
        });
      },
      getStu(cur, list) {
        var self = this;
        API.choolStu(cur, list, self.school_id).then(res => {
          self.loading = false;
          self.tableData = res.data;
          self.total = res.total;
        }).catch(err => {
          self.loading = false;
        })
      },
      selcScl(val) {
        var self = this;
        self.school_id = val;
        self.loading = true;
        self.student = '';
        self.current = 1;
        self.getStu(self.current, self.size);
      },

      // 
      getGrade() {
        var self = this;
        API.grades(self.current).then((res) => {
          self.gradeList = res.data;
        });
      },
      getClass(grade_id) {
        var self = this;
        API.classes(1, 1000, grade_id).then((res) => {
          self.classList = res.data;
        });
      },
      // 
      gradeOnChange(value) {
        var self = this;
        self.grade = value;
        self.getClass(value);
        self.form.grade_id = value;
      },
      // 
      classOnChange(value) {
        var self = this;
        self.form.class_id = value;
      },
      addStudent() {
        var self = this;
        self.dialogStudent = true;
      },
      close() {
        var self = this;
        self.grade = "";
        self.classValue = "";
        self.form = {
          name: "",
          number: "",
          sex: 1,
          age: "",
          class_id: "",
        };

      },
      newStudent() {
        var self = this;
        if (self.form.name && self.form.number && self.form.age && self.grade && self.form.class_id && self.form.age !=
          0) {
          API.student(self.form).then((res) => {
            self.$message.success("");
            self.getStudent(self.current, self.size);
            self.dialogStudent = false;
          });
        } else {
          self.$message.warning("");
        }
      },

      // 
      // 
      handleFamily(index, row) {
        var self = this;
        self.bindFormFamily.student_id = row.id;
        self.dialogFamily = true;
        self.familyList = [];
        self.keyword = ''
      },
      // 
      searchFamily(keyword) {
        var self = this;
        API.seekUser(self.keyword).then((res) => {
          self.familyList = res;
        });
      },
      // 
      bindFamily(index, row) {
        var self = this;
        self.bindFormFamily.user_id = row.user_id;
        API.bindFamily(self.bindFormFamily).then((res) => {
          self.$message.success("");
          self.getStudent(self.current, self.size);
          self.bindFormFamily.user_id = "";
          self.dialogFamily = false;
          self.familyList = [];
          self.keyword = "";
        });
      },
      // 
      handleFace(index, row) {
        var self = this;
        self.dialogFace = true;
        self.familyForm.id = row.id;
        self.familyForm.only_in = row.only_in;
        self.old_href = row.face_image;
      },

      // 
      openFullScreen() {
        const loading = this.$loading({
          lock: true,
          text: 'Loading',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
        return loading;
      },
      closeFullScreen(loading) {
        loading.close();
      },

      // 
      handleForbidden(index, row) {
        var self = this;
        self.forbidden_id = row.id;
        if (row.face_id && row.face_image) {
          self.dialogForbidden = true;
        } else {
          self.$message.error(", !");
        }
      },
      forbiddenFace() {
        var self = this;
        var student = "student";
        self.openFullScreen();
        API.failFace(self.forbidden_id, student)
          .then((res) => {
            self.dialogForbidden = false;
            self.closeFullScreen(self.openFullScreen());
            self.$message.success("");
          })
          .catch((err) => {
            self.closeFullScreen(self.openFullScreen());
          });
      },

      // 
      handlePushFace(index, row) {
        var self = this;
        if (row.school && row.school.offline == 1) {
          self.hasUuid = true;
        }
        self.openFace_id = row.id;
        if (row.face_id && row.face_image) {
          self.dialogPushFace = true;
        } else {
          self.$message.error(", !");
        }
      },
      inputUuid(value) {
        var self = this;
        self.uuid = value;
      },
      pushFace() {
        var self = this;
        var student = "student";
        self.openFullScreen();
        if (self.uuid) {
          API.pushFace1(self.openFace_id, student, self.uuid)
            .then((res) => {
              self.dialogPushFace = false;
              self.uuid = "";
              self.closeFullScreen(self.openFullScreen());
              self.$message.success("");
            })
            .catch((err) => {
              self.closeFullScreen(self.openFullScreen());
            });
        } else {
          API.pushFace(self.openFace_id, student)
            .then((res) => {
              self.dialogPushFace = false;
              self.closeFullScreen(self.openFullScreen());
              self.$message.success("");
            })
            .catch((err) => {
              self.closeFullScreen(self.openFullScreen());
            });
        }
      },

      // 
      handleCreateFace(index, row) {
        var self = this;
        self.id = row.id;
        if (row.face_id && row.face_image) {
          self.dialogCreateFace = true;
        } else {
          self.$message.error(", !");
        }
      },
      createFace() {
        var self = this;
        self.openFullScreen();
        API.createFace(self.id)
          .then((res) => {
            self.dialogCreateFace = false;
            self.closeFullScreen(self.openFullScreen());
            self.$message.success("");
          })
          .catch((err) => {
            self.closeFullScreen(self.openFullScreen());
          });
      },

      // 
      handleServe(index, row) {
        var self = this;
        self.id = row.id;
        self.dialogOpenedServe = true;
        API.studentServe(self.id).then((res) => {
          self.$message.success("");
          self.serviceList = res;
        });
      },

      // 
      handleEdit(index, row) {
        var self = this;
        self.dialogStudent = true;
        row.sex = Number(row.sex);
        self.form = row;
        self.grade = row.grade_id;
        self.classValue = row.class;
      },

      // 
      handleDel(index, row) {
        var self = this;
        self.dialogDel = true;
        self.id = row.id;
      },
      toDel() {
        var self = this;
        API.delStudent(self.id).then((res) => {
          self.$message.success("");
          self.dialogDel = false;
          self.getStudent(self.current, self.size);
        });
      },

      // 
      fucLog(cur, list) {
        var self = this;
        API.faceLogs(cur, list, self.log_id).then((res) => {
          self.$message.success("!");
          self.FaceLogsDate = res.data;
          self.logsTotal = res.total;
          self.FaceLogsDate.forEach((item) => {
            item.timestamp = DATE.formatTime(item.timestamp, "Y-M-D h:m:s");
          });
        });
      },
      handleFaceLogs(index, row) {
        var self = this;
        self.dialogFaceLogs = true;
        self.log_id = row.id;
        self.logsCurrent = 1;
        self.logsSize = 10;
        self.fucLog(self.logsCurrent, self.logsSize);
      },
      // 
      logsCurrentChange(val) {
        var self = this;
        self.logsCurrent = val;
        self.fucLog(val, self.logsSize);
      },
      logSizeChange(val) {
        var self = this;
        self.logsSize = val;
        self.fucLog(1, val);
        self.logsCurrent = 1;
      },

      // 
      handleChange(file) {
        var self = this;
        self.change_href = URL.createObjectURL(file.raw);
        self.hasNewImage = true;
      },
      handleRemove(file) {
        var self = this;
        self.change_href = "";
        self.hasNewImage = false;
      },
      beforeAvatarUpload(file) {
        var self = this;
        self.fileName = md5(file.name);
        self.suffix = file.name.substring(file.name.lastIndexOf(".") + 1);
        self.imgData.key = `tmp_${self.fileName}.${self.suffix}`;
      },
      changeFace() {
        var self = this;
        if (self.change_href === "") {
          self.familyForm.href = self.old_href;
          API.studentFace(self.familyForm).then((res) => {
            self.$message.success("");
            self.getStudent(self.current, self.size);
            self.familyForm.href = "";
            self.dialogFace = false;
          });
        } else {
          this.$refs.upload.submit();
        }
      },
      handleAvatarSuccess(res, file) {
        var self = this;
        file.url = `${self.qiniuaddr}/${res.key}`;
        self.familyForm.href = file.url;
        API.studentFace(self.familyForm).then((res) => {
          self.$message.success("");
          self.getStudent(self.current, self.size);
          self.$refs.upload.clearFiles();
          self.familyForm.href = "";
          self.change_href = "";
          self.old_href = "";
          self.imgData.key = "";
          self.familyForm.user_id = "";
          self.dialogFace = false;
        });
      },
      handleExceed(file, fileList) {
        //
        var self = this;
        self.$message.error("1!");
      },
      getQiniuToken() {
        var self = this;
        axios
          .get("https://api.fengniaotuangou.cn/api/upload/token")
          .then((res) => {
            self.imgData.token = res.data.uptoken;
          });
      },
    },
  };
</script>

